import React, { memo } from "react";
import { useTranslation } from "react-i18next";
import Modal from "react-modal";
import { getUrlQueryParameters } from "../../data/tutorialData";
import { useStore } from "../../store";
import { translate } from "../../utils/i18next.utils";
import "./index.scss";
const customStyles: Modal.Styles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
    borderColor: "transparent",
    backgroundColor: "transparent"
  }
};
if (process.env.NODE_ENV !== "test") {
  Modal.setAppElement("#root");
}

interface IModalComponentProps {
  isOpen: boolean;
  closeModal: () => void;
}

const ModalComponent = ({ isOpen, closeModal }: IModalComponentProps) => {
  const { pushHistoryState } = useStore((state) => ({
    pushHistoryState: state.pushHistoryState
  }));
  const onClickHandler = () => {
    const link = getUrlQueryParameters("Welcome/Welcome.json");
    pushHistoryState(link);
  };
  const { t: fn } = useTranslation();
  const t = translate(fn);
  return (
    <Modal
      isOpen={isOpen}
      onRequestClose={closeModal}
      shouldCloseOnEsc={true}
      shouldCloseOnOverlayClick={false}
      style={customStyles}
      overlayClassName="Overlay"
    >
      <div tabIndex={-1} role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div className="modal-dialog modal-dialog-centered modal-dialog-scrollable" role="document">
          <div className="modal-content">
            <div className="modal-header">
              <h1 data-testid="AznblpNewDiagTitle" className="modal-title" id="exampleModalLabel">
                {t("AznblpNewDiagTitle")}
              </h1>
            </div>
            <div data-testid="AznblpNewDiagBody" className=" mcBackgroundN6-BHalf-2 modal-style-2 modal-body">
              {t("AznblpNewDiagBody")}
              <div className="modal-body-button-stack">
                <button
                  data-testid="AznblpNewDiagContButton"
                  type="button"
                  className="__ButtonGrey"
                  onClick={onClickHandler}
                >
                  {t("AznblpNewDiagContButton")}
                </button>
                <button
                  data-testid="AznblpNewDiagCancelButton"
                  type="button"
                  className="__ButtonGrey"
                  onClick={closeModal}
                >
                  {t("AznblpNewDiagCancelButton")}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Modal>
  );
};
if (process.env.NODE_ENV === "development") {
  ModalComponent.whyDidYouRender = true;
}

export const LandingModal = memo(ModalComponent);
