import React, { memo } from "react";
import { ITutorial } from "../../data/tutorialData";
import { useStore } from "../../store";
const imgPath = process.env.PUBLIC_URL + "/assets/img/";

interface ITutorialProps {
  tutorial: ITutorial;
}
const Tutorial = memo(({ tutorial }: ITutorialProps) => {
  const { pushHistoryState } = useStore((state) => ({
    pushHistoryState: state.pushHistoryState
  }));

  const handleClick = () => {
    pushHistoryState(tutorial.link);
  };

  return (
    <a onClick={handleClick}>
      <div className="card edu-notebook-card card--black selectable">
        <img src={imgPath + tutorial.imgUrl} width={273} height={153} role="img" aria-label={tutorial.description} />
        <div className="card-body">
          <div className="card-title">
            <h1>{tutorial.title}</h1>
          </div>
          <p className="card-text">{tutorial.description}</p>
        </div>
      </div>
    </a>
  );
});
Tutorial.displayName = "Tutorial";
export { Tutorial };
