import React, { memo } from "react";
import { ITutorial } from "../../data/tutorialData";
import { translate } from "../../utils/i18next.utils";
import { useTranslation } from "react-i18next";
import { Tutorial } from "./Tutorial";
import "./index.scss";
export interface ITutorialsProps {
  tutorials: ITutorial[];
}

const Tutorials = memo(({ tutorials }: ITutorialsProps) => {
  const { t: fn } = useTranslation();
  const t = translate(fn);
  return (
    <div className="edu-notebook-card-section  tutorials-container">
      <div className="card-section-header">
        <h2 className="card-section-title">{t("AznblpSectHeaderTut")}</h2>
        <p className="card-section-desc">{t("AznblpSectBodyTut")}</p>
      </div>
      <div className="card-row">
        {tutorials.map((tutorial: ITutorial) => (
          <Tutorial tutorial={tutorial} key={tutorial.link} />
        ))}
      </div>
    </div>
  );
});
Tutorials.displayName = "Tutorials";
export { Tutorials };
