export interface ITutorial {
  imgUrl: string;
  title: string;
  description: string;
  link: string;
}

export const getUrlQueryParameters = (link: string) => {
  const result = new URL(window.location.href);
  result.searchParams.get("lesson") ? undefined : result.searchParams.append("lesson", `${link}`);
  result.searchParams.append("source", "remote");
  return result.search;
};
