import React, { Component } from "react";
import "./index.scss";

type Props = {
  children: React.ReactNode;
};

export class PageHeader extends Component<Props> {
  static displayName = "PageHeader";
  render() {
    return <div className="page-wrapper-header">{this.props.children}</div>;
  }
}

export class PageBody extends Component<Props> {
  static displayName = "PageBody";
  render() {
    return <div className="page-wrapper-body">{this.props.children}</div>;
  }
}
export class PageFooter extends Component<Props> {
  static displayName = "PageFooter";
  render() {
    return <div className="page-wrapper-footer">{this.props.children}</div>;
  }
}

export class Page extends Component<Props> {
  static displayName = "Page";
  static Header = PageHeader;
  static Body = PageBody;
  static Footer = PageFooter;

  render() {
    return <div className="page-wrapper">{this.props.children}</div>;
  }
}
