import React, { memo, useCallback, useState } from "react";
import { useTranslation } from "react-i18next";
import { translate } from "../../utils/i18next.utils";
import { LandingModal } from "../landing-modal";
const imgPath = process.env.PUBLIC_URL + "/assets/img";
const backgroundImage: React.CSSProperties = { backgroundImage: `url(${imgPath}/agent_panorama.png)` };

const Header = memo(() => {
  const [isModalOpen, setModal] = useState(false);
  const handleOpenModal = () => {
    setModal(true);
  };
  const handleCloseModal = useCallback(() => {
    setModal(false);
  }, [setModal]);
  const { t: fn } = useTranslation();
  const t = translate(fn);
  return (
    <>
      <LandingModal isOpen={isModalOpen} closeModal={handleCloseModal} />

      <div className="edu-notebook-landing-header">
        <div className="jumbotron jumbotron-fluid" style={backgroundImage}>
          <div className="container">
            <h1 data-testid="AznblpTitle" className="display-4">
              {t("AznblpTitle")}
            </h1>
            <p data-testid="AznblpTopBarBody" className="lead">
              {t("AznblpTopBarBody")}
            </p>
          </div>
        </div>
        <div id="new-proj-btn" className="button-row">
          <button
            type="button"
            data-testid="AznblpNewProjButton"
            className="__ButtonLiteGrey header-button"
            role="button"
            onClick={handleOpenModal}
            data-original-title={t("AznblpNewProjButton")}
          >
            <i className="icon icon_plus" aria-hidden="true"></i> {t("AznblpNewProjButton")}
          </button>
        </div>
        <div className="edu-notebook-section-divider  edu-notebook-divider" />
      </div>
    </>
  );
});
Header.displayName = "Header";
export { Header };
